'use strict';

angular.module('employerModule',['api', 'toastr','config','underscore','LocalStorageModule','cache']);
'use strict';
angular.module('employerModule').controller('VideoInterviewController', function($scope, $employer,
    $translate, $rootScope, localStorageService, $location, toastr)
{
    $scope.gotoSettingInterview = function()
    {
        if (!$scope.video_interview.$error.required)
        {
            localStorageService.set('interview', $scope.interview);
            $location.path("/employer/video/setting_interview");
        }
        else
        {
            var alert = $translate.instant('toarster.alert');
            var required = $translate.instant('toarster.required');
            toastr.error(required, alert);
        }
    };
    
    function init()
    {
        $scope.userEmployer = localStorageService.get('userEmployer');
        if (!$scope.userEmployer)
        {
            $location.path("/");
            return;
        }
        $scope.chooseAssignment = localStorageService.get('chooseAssignment');
        if (localStorageService.get('interview')){
            $scope.interview = localStorageService.get('interview');
        } else {
            $scope.interview = {
                name: $scope.chooseAssignment.name,
                mode: 'video',
                language: 'vi',
                response:1,
                prepare:1
            };
        }
    }
    
   

    init();
});
'use strict';
angular.module('employerModule').controller('SettingVideoInterviewController', function($scope, $employer,
    $translate, $rootScope, localStorageService, $location, $common, $route, $q, toastr)
{
    function init()
    {
        $scope.interview = localStorageService.get('interview');
        $scope.uploadFile = {
            intro: null,
            exit: null
        };
        $scope.userEmployer = localStorageService.get("userEmployer");
        if (!$scope.userEmployer)
        {
            $location.path("/");
            return;
        }
    }
    
    $scope.backInterview = function()
    {
        localStorageService.set('interview', $scope.interview);
        $location.path("/employer/video/interview");
    };

    function uploadVideo(file, urlProp)
    {
            return $q(function(resolve) {
            if (file)
            {
                $common.uploadVideo(
                {
                    file: file
                }, function(result)
                {
                    if (result.status && result.data.result)
                    {
                        $scope.interview[urlProp] = result.data.url;
                    }
                    resolve();
                });
            }
            else
            {
                if (!$scope.interview[urlProp]){
                    $scope.interview[urlProp] = "";
                }
                resolve();
            }
        });
    }
    $scope.gotoQuestionInterview = function()
    {
        if (!$scope.setting_video.$error.required)
        {
            uploadVideo($scope.uploadFile.intro, "introUrl")
            .then(function() {
                uploadVideo($scope.uploadFile.exit, "exitUrl")
                .then(function() {
                    localStorageService.set('interview', $scope.interview);
                $location.path("/employer/video/question_interview");
                });
            });
        }
        else
        {
            var alert = $translate.instant('toarster.alert');
            var required = $translate.instant('toarster.required');
            toastr.error(required,alert);
        }
    };

    init();
});
'use strict';
angular.module('employerModule').controller('ReviewConfirmVideoInterviewController', function($scope,
    $employer, $translate, $rootScope, localStorageService, _, $location,$q)
{
    var chooseAssignment = localStorageService.get("chooseAssignment");
    function init()
    {
        $scope.userEmployer = localStorageService.get("userEmployer");
        if (!$scope.userEmployer)
        {
            $location.path("/");
            return;
        }
        $scope.interview = localStorageService.get("interview");
        $scope.interviewQuestionList = localStorageService.get("interviewQuestionList");
        $scope.company = localStorageService.get('companyProfile');
        $scope.interview.aboutUsUrl = $scope.company.videoUrl;
    }

    function _createInterviewQuestion(listQuestion, interviewId)
    {
        return $q(function(resolve, reject) {
            if (listQuestion.length === 0){
                resolve();
            }
            else {
                var info = {
                        token: $scope.userEmployer.token,
                        interviewId: interviewId,
                        questionList: listQuestion
                    };
                    $employer.addInterviewQuestion(info, function(result)
                    {
                        if (result.status){
                            resolve();
                        } else {
                            reject();
                        }
                    });
            }
        });
      
    }
    function _updateInterviewQuestion(listQuestion, interviewId)
    {
        return $q(function(resolve, reject) {
            if (listQuestion.length === 0){
                resolve();
            }
            else {
                var info = {
                        token: $scope.userEmployer.token,
                        interviewId: interviewId,
                        questionList: listQuestion
                    };
                $employer.updateInterviewQuestion(info, function(result)
                {
                    if (result.status) {
                        resolve();
                    } else {
                        reject();
                    }
                });
            }
        });
    }
    var _removeInterviewQuestion = function(listQuestion)
    {
        return $q(function(resolve, reject) {
            if (listQuestion.length === 0){
                resolve();
            }
            else {
                var questionIds = [];
                listQuestion.forEach(function(question)
                {
                    questionIds.push(question.id);
                });
                var info = {
                    token: $scope.userEmployer.token,
                    questionIds: questionIds
                };
                $employer.removeInterviewQuestion(info, function(result)
                {
                    if (result.status) {
                        resolve();
                    } else {
                        reject();
                    }
                });
            }
        });
    };
    
    function _createOrUpdateInterview()
    {
        return $q(function(resolve, reject) {
            if ($scope.interview.id)
            {
                $employer.updateInterview(
                {
                    token: $scope.userEmployer.token,
                    interview: $scope.interview
                }, function(result)
                {
                    if (result.status && result.data.result){
                        resolve($scope.interview.id);
                    } else {
                        reject();
                    }
                });
            }
            else
            {
                $employer.createInterview(
                {
                    token: $scope.userEmployer.token,
                    assignmentId: chooseAssignment.id,
                    interview: $scope.interview
                }, function(result)
                {
                    if (result.status && result.data.result){
                        resolve(result.data.interviewId);
                    } else {
                        reject();
                    }
                });
            }
        });
       
    }
    
    function _createOrUpdateInterviewQuestion(interviewId)
    {
        var createInterviewQuestion = _.filter($scope.interviewQuestionList, function(question)
        {
            return !question.id;
        });
        var updateInterviewQuestion = _.filter($scope.interviewQuestionList, function(question)
        {
            return question.id && !question.remove;
        });
        var removeInterviewQuestion = _.filter($scope.interviewQuestionList, function(question)
        {
            return question.id && question.remove;
        });
        return $q(function(resolve) {
            _createInterviewQuestion(createInterviewQuestion, interviewId)
            .then( _updateInterviewQuestion(updateInterviewQuestion, interviewId))
            .then( _removeInterviewQuestion(removeInterviewQuestion))
            .then(resolve());
        });
    }
    
    $scope.doSave = function()
    {
        _createOrUpdateInterview().then(function(interviewId)
        {
            if (interviewId)
            {
                _createOrUpdateInterviewQuestion(interviewId).then(function() {
                    localStorageService.remove("interviewQuestionList");
                    localStorageService.remove("interview");
                    $location.path("/employer/interview_list");
                });
            }
        });
    };
    init();
});
'use strict';
angular.module('employerModule')
.filter('category', function(_) {
    return function(questionList, categoryId) {
        return  _.filter(questionList, function(question)
                {
                    return question.categoryId === categoryId;
                });
    };
  })
.filter('existQuestion', function(_) {
    return function(questionList, existList) {
        return _.filter(questionList, function(question)
        {
            return !_.find(existList,function(existQ) {
                return existQ.questionId === question.id;
            });
        });
    };
  })
   .filter('freeText', function(_) {
    return function(questionList) {
        return _.filter(questionList, function(question)
        {
            return !question.type || question.type ==='ET';
        });
    };
  })
.controller('QuestionVideoInterviewController', function($scope, $employer, $rootScope, localStorageService, $location, $common, $q, $cache, _)
{
    
    function getInterviewQuestion()
    {
        return $q(function(resolve) {
            if (localStorageService.get("interviewQuestionList"))
            {
                resolve(localStorageService.get("interviewQuestionList"));
            } else
            {
                if ($scope.interview.id)
                {
                    $employer.getInterviewQuestion(
                    {
                        token: $scope.userEmployer.token,
                        interviewId: $scope.interview.id
                    }, function(result)
                    {
                        if (result.status)
                        {
                            localStorageService.set("interviewQuestionList", result.data.questionList);
                        }
                        resolve(localStorageService.get("interviewQuestionList"));
                    });
                } else {
                    resolve([]);              
                }
            }
        });         
    }
    
    function getQuestionCategory(lang)
    {
        return $q(function(resolve, reject) {
            $cache.getQuestionCategory({  lang: lang }, function(result)
            {
                if (result.status)
                {
                    resolve(result);
                }
                reject();
            });  
        });    
    }
    function getQuestion(lang)
    {
        return $q(function(resolve, reject) {
            $cache.getQuestion({lang: lang }, function(result)
            {
                if (result.status)
                {
                    resolve(result);
                }
                reject();
            });   
        });   
    }

    function resetInterviewQuestion() {
        var lastCategoryId = null;
        if ($scope.questionCategory){
            lastCategoryId = $scope.questionCategory[0].id;
        }
        if ($scope.interviewQuestion && $scope.interviewQuestion.categoryId){
            lastCategoryId = $scope.interviewQuestion.categoryId;
        }
        $scope.interviewQuestion = {
                questionFile: null,
                questionUrl:null,
                response: $scope.interview.response,
                prepare: $scope.interview.prepare,
                title:"",
                categoryId:lastCategoryId,
                questionType:"ET"
            };
    }
    
    function init()
    {
        $scope.interview = localStorageService.get('interview');
        $scope.userEmployer = localStorageService.get('userEmployer');
        if (!$scope.userEmployer)
        {
            $location.path("/");
            return;
        }
        resetInterviewQuestion();
        getInterviewQuestion()
        .then(function(interviewQuestionList) {
            $scope.interviewQuestionList = interviewQuestionList;
        });
    }
    
    $rootScope.$watch('language', function(newValue, oldValue)
    {
        if (newValue) {
            getQuestionCategory(newValue).
            then( function (result) {
                $scope.questionCategory = result.data.categoryList;
                getQuestion(newValue)
                .then ( function (result) {
                    $scope.questionList = result.data.questionList;
                    $scope.interviewQuestion.categoryId = $scope.questionCategory[0].id;
                    $scope.interviewQuestion.questionId = $scope.questionList[0].id;
                });
            });
        }
    }); 

    function addQuestionSystem(questionId) {
        var question = _.find($scope.questionList,function(q) {
            return q.id === questionId;
        });
        var questionCount = _.countBy($scope.interviewQuestionList, function(question) {
          return !question.remove;
        });
        if(!questionCount.true) {
            questionCount = {
                true : 0
            };
        }
        $scope.interviewQuestion.title = question.title;
        $scope.interviewQuestion.questionId = question.id;
        $scope.interviewQuestion.order = questionCount.true + 1;
        $scope.interviewQuestion.source = "system";
        $scope.interviewQuestion.type = "text";
        $scope.interviewQuestion.videoUrl = "";
        $scope.interviewQuestionList.push($scope.interviewQuestion);
        localStorageService.set("interviewQuestionList", $scope.interviewQuestionList);
        resetInterviewQuestion();
    }     
    
    $scope.chooseQuestion = function()
    {
        addQuestionSystem($scope.interviewQuestion.questionId);      
    };
    
    $scope.removeInterviewQuestion = function(removedQuestion)
    {
        if (removedQuestion.id) {
            removedQuestion.remove = true;
        } else {
            $scope.interviewQuestionList = _.reject($scope.interviewQuestionList, function(question)
            {
                return question.title === removedQuestion.title;
            });
        }
        var order = 1;
        for (var i = 0; i < $scope.interviewQuestionList.length; i++) {
            if (!$scope.interviewQuestionList[i].remove) {
                $scope.interviewQuestionList[i].order =  order++;
            }
        }
        localStorageService.set("interviewQuestionList", $scope.interviewQuestionList);
    };
    
    $scope.moveDownInterviewQuestion = function(interviewQuestion)
    {
        var existQuestion = _.find($scope.interviewQuestionList, function(question)
        {
            return !question.remove && question.order > interviewQuestion.order;
        });
        if (existQuestion)
        {
            var order = existQuestion.order;
            existQuestion.order = interviewQuestion.order;
            interviewQuestion.order = order;
        }
    };

    function addQuestionSearch(question) {
        var questionCount = _.countBy($scope.interviewQuestionList, function(question) {
          return !question.remove;
        });
        if(!questionCount.true) {
            questionCount = {
                true : 0
            };
        }
        $scope.interviewQuestion.title = question.title;
        $scope.interviewQuestion.questionId = question.id;
        $scope.interviewQuestion.order = questionCount.true + 1;
        $scope.interviewQuestion.source = "system";
        $scope.interviewQuestion.type = "text";
        $scope.interviewQuestion.videoUrl = "";
        $scope.interviewQuestionList.push($scope.interviewQuestion);
        localStorageService.set("interviewQuestionList", $scope.interviewQuestionList);
        resetInterviewQuestion();
    }
    $scope.chooseQuestionSearch = function(question)
    {
        addQuestionSearch(question);       
    };   
     
    $scope.addQuestionManual = function()
    {
        var questionCount = _.countBy($scope.interviewQuestionList, function(question) {
          return !question.remove;
        });
        if(!questionCount.true) {
            questionCount = {
                true : 0
            };
        }
        $scope.interviewQuestion.order = questionCount.true + 1;
        $scope.interviewQuestion.source = "manual";
        $scope.interviewQuestion.type = "text";  
        $scope.interviewQuestion.videoUrl = "";      
        
        if ($scope.interviewQuestion.questionFile)
        {
            $common.uploadVideo(
            {
                file: $scope.interviewQuestion.questionFile
            }, function(result)
            {
                $scope.$apply();
                if (result.status && result.data.result)
                {
                    $scope.interviewQuestion.videoUrl = result.data.url;
                    $scope.interviewQuestion.type = "video";
                    $scope.interviewQuestionList.push($scope.interviewQuestion);
                    localStorageService.set("interviewQuestionList", $scope.interviewQuestionList);
                    resetInterviewQuestion();
                    $scope.$apply();
                }
            });
        } else {
            $scope.interviewQuestionList.push($scope.interviewQuestion);
            localStorageService.set("interviewQuestionList", $scope.interviewQuestionList);
            resetInterviewQuestion();
        }
    };

    $scope.backSetting = function()
    {
        localStorageService.set('interview', $scope.interview);
        $location.path("/employer/video/setting_interview");
    };
    $scope.gotoReview = function()
    {
         localStorageService.set('interview', $scope.interview);
         $location.path("/employer/video/review_confirm_interview");
    };
    init();
});
'use strict';
angular.module('employerModule').controller('SettingQuizInterviewController', function($scope,
    $employer, $rootScope, localStorageService, $location, $common, $route, $q,$log)
{
    var init = function()
    {
        $scope.interview = localStorageService.get('interview');
        $scope.uploadFile = {
            intro: null,
            exit: null
        };
        $scope.videoControl = {
            intro: null,
            exit: null
        };
        $scope.userEmployer = localStorageService.get("userEmployer");
        if (!$scope.userEmployer)
        {
            $location.path("/");
            return;
        }
    };
    $scope.backInterview = function()
    {
        localStorageService.set('interview', $scope.interview);
        $location.path("/employer/quiz/interview");
    };
    var uploadVideo = function(file, urlProp, defer)
    {
        if (file)
        {
            $common.uploadVideo(
            {
                file: file
            }, function(result)
            {
                if (result.status && result.data.result)
                {
                    $scope.interview[urlProp] = result.data.url;
                    $log.info($scope.interview);
                }
                defer.resolve();
            });
        }
        else
        {
            if (!$scope.interview[urlProp]) {
                $scope.interview[urlProp] = "";
            }
            defer.resolve();
        }
    };
    $scope.gotoQuestionInterview = function()
    {
        var deferArray = [$q.defer(), $q.defer()];
        var loopPromises = [];
        angular.forEach(deferArray, function(deferItem)
        {
            loopPromises.push(deferItem.promise);
        });
        deferArray[0].promise.then(function()
        {
            uploadVideo($scope.uploadFile.exit, "exitUrl", deferArray[1]);
        });
        uploadVideo($scope.uploadFile.intro, "introUrl", deferArray[0]);
        $q.all(loopPromises).then(function()
        {
            localStorageService.set('interview', $scope.interview);
            $location.path("/employer/quiz/question_interview");
        });
    };
    $scope.doCancel = function()
    {
        $location.path("/employer/interview_list");
    };
    init();
});
