'use strict';
angular.module('employerModule').controller('ConferenceListController', function($scope, $employer,
    $translate, $rootScope, localStorageService, $location, $window)
{
    // var alert = $translate.instant('toarster.alert');
    // var unablejob = $translate.instant('toaster.unablejob');
    // var serverError = $translate.instant('toarster.serverError');
    $scope.launchConference = function(conference)
    {
        conference.memberList.forEach(function(member)
        {
            if (member.role === 'moderator')
            {
                if (conference.status === 'pending')
                {
                    var url1 = "/#/conference?meetingId=" + conference.meetingId + "&memberId=" + member.memberId + "&translate=" + conference.language;
                    $window.open(url1,'_blank');
                    
                    $employer.openMeeting(
                    {
                        token: $scope.userEmployer.token,
                        conferenceId: conference.id
                    }, function()
                    {
                        
                    });
                }
                else
                {
                    var url = "/#/conference?meetingId=" + conference.meetingId + "&memberId=" + member.memberId + "&translate=" + conference.language;
                    $window.open(url,'_blank');
                }
            }
        });
    };

    function  getConference()
    {
        $employer.getConference(
        {
            token: $scope.userEmployer.token
        }, function(result)
        {
            if (result.status && result.data.result)
            {
                $scope.listConference = result.data.conferenceList;
                localStorageService.set("listConference", $scope.listConference);
                $scope.totalItems = $scope.listConference.length;
            }
        });
    }
    var init = function()
    {
        $scope.userEmployer = localStorageService.get('userEmployer');
        if (!$scope.userEmployer)
        {
            $location.path("/");
            return;
        }
        $scope.listConference = [];
        getConference();
    };
    init();
});
'use strict';
angular.module('employerModule')
.controller('CandidateListController', function($rootScope, $scope, $common, $filter, $employer, $q, $employee, $location, $translate, $window, $log, $uibModal, localStorageService, _) {
        $scope.employer = localStorageService.get('userEmployer');

    if (!$scope.employer)
        {
            $location.path("/");
            return;
        }
    var config = {
        countryId: 243, // Viet Nam
        provinceId: 60 // Ha Noi
    };
    var onecv = $translate.instant('employer.tencv');
    var twocv = $translate.instant('employer.twentycv');
    var threecv = $translate.instant('employer.thirtycv');
    var fourcv = $translate.instant('employer.fourtycv');
    var fivecv = $translate.instant('employer.fivetycv');
    // var alllocation = $translate.instant('employee.searchPostions');
    
    $scope.searching = false;
    $scope.resultSearch = true;
    $scope.countriesList = [];
    $scope.provincesList = [];
    $scope.categoriesList = [];
    // Pagination
    $scope.candidateListPagination = [];
    $scope.totalCandidates = 0;
    $scope.currPage = 1;
    $scope.itemsPerPage = 10;
    $scope.maxSize = 4;
    $scope.search = {
        options: {
            email: '',
            countryId: '',
            provinceId: '',
            categoryId: '',
            positionId: ''
        }
    };
    $scope.optionNumbers = {
        availableNumbers: [
                            { id: 1, value: 10, lable: onecv }, 
                            { id: 2, value: 20, lable: twocv }, 
                            { id: 3, value: 30, lable: threecv },
                            { id: 4, value: 40, lable: fourcv },
                            { id: 5, value: 50, lable: fivecv }
                         ],
        selectedNumber: { id: 1, value: 10 }
    };

    // Get countries and provinces list
    var getLocation = function() {
        var deferred = $q.defer();
            if (!localStorageService.get('countriesList') || !localStorageService.get('provincesList')) {
                $common.getJobLocation({ lang: 'vi' }, function(result){
                  if(result.status){
                    result.data.countryList.unshift({
                        id: '',
                        title: 'Tất cả đất nước'
                    });
                    result.data.provinceList.unshift({
                        id: '',
                        title: 'Tất cả tỉnh/thành phố'
                    });

                    $scope.countriesList = result.data.countryList;
                    $scope.provincesList = result.data.provinceList;
                    
                    localStorageService.set('countriesList', result.data.countryList);
                    localStorageService.set('provincesList', result.data.provinceList);
                    deferred.resolve(true);
                  } else {
                    // $log.info('error', 'Khong lay duoc danh sach country va province');
                    deferred.resolve(true);
                  }
                });      
            } else {
                $scope.countriesList = localStorageService.get('countriesList');
                $scope.provincesList = localStorageService.get('provincesList');  
                deferred.resolve(true);
            } 

        return deferred.promise;
    };

    // Get categories list
    var getCategories = function(lang) {
        var deferred = $q.defer();

        if(!localStorageService.get('categoriesList_' + lang)){
            $common.getJobCategory({ 'lang': lang }, function(result){
                if(result.status){
                    $scope.categoriesList = result.data.categoryList;
                    localStorageService.set('categoriesList_' + lang, result.data.categoryList);
                    deferred.resolve(true);
                } else {
                    // $log.info('Khong lay duoc danh sach category');
                    deferred.resolve(false);
                }            
            });
        } else{
            $scope.categoriesList = localStorageService.get('categoriesList_' + lang);
            deferred.resolve(true);
        }

        return deferred.promise;
    };

    // Set current country
    var setCurrCountry = function(countryId) {
        $scope.currCountry = _.find($scope.countriesList, function(country) {
            return (country.id === countryId);
        });

        $scope.countries = {
            availableCountries: $scope.countriesList,
            selectedCountry: $scope.currCountry
        };
    };

    // Set current province
    var setCurrProvince = function(provinceId) {
        $scope.currProvince = _.find($scope.provincesList, function(province) {
            return (province.id === provinceId);
        });

        $scope.provinces = {
            availableProvinces: $scope.provincesList,
            selectedProvince: $scope.currProvince
        };
    };

    var calcPagination = function(currPage) {
        var start = (currPage - 1)*$scope.itemsPerPage;
        var end = currPage*$scope.itemsPerPage;

        $scope.currPage = currPage;
        $scope.candidateListPagination = $scope.candidatesList.slice(start, end);
    };

    var getFullname = function(candidatesList) {
        candidatesList.map(function(candidate) {

            if (candidate.name.indexOf('-') !== -1) {
                var firstname = candidate.name.split('-')[0];
                var lastname = candidate.name.split('-')[1];
                candidate.fullname = firstname + ' ' + lastname;
            } else {
                candidate.fullname = candidate.name;
            }

            return candidate;
        });
    };

    var getLocationName = function(candidatesList, countriesList, provincesList) {
        candidatesList.map(function(candidate) {
            // Get country name
            var country = _.find(countriesList, function(country) {
                return (candidate.countryId === country.id);
            });

            candidate.countryName = country ? country.title : '';

            // Get province name
            var province = _.find(provincesList, function(province) {
                return (candidate.provinceId === province.id);
            });

            candidate.provinceName = province ? province.title : '';

            return candidate;
        });
    };

    var getCategoryName = function(categoryId, categoriesList) {
        var category = _.find(categoriesList, function(category) {
            return (categoryId === category.id);
        });

        var categoryName = category ? category.title : null;
        return categoryName;
    };

    var getInfoObj = function() {
        $scope.search.options.countryId = config.countryId;
        $scope.search.options.provinceId = $scope.provinces.selectedProvince.id;
        $scope.search.options.positionId = $scope.positions.seletedPosition;

        var info = {
            token: $scope.employer.token,
            option: $scope.search.options
        };

        return info;
    };

    var setSearchResult = function(candidateList) {
        $scope.candidatesList = candidateList;
        $scope.totalCandidates = $scope.candidatesList.length;

        $scope.candidatesList.map(function(candidate) {
            candidate.categories = candidate.categoryIds.map(function(categoryId) {
                var category = {
                    id: categoryId,
                    title: getCategoryName(categoryId, $scope.categoriesList)
                };
                return category;
            });
        });

        getFullname($scope.candidatesList);
        getLocationName($scope.candidatesList, $scope.countriesList, $scope.provinces.availableProvinces);
        calcPagination($scope.currPage);
    };

    var getSearchResult = function(info) {
        var deferred = $q.defer();

        $employer.searchCandidate(info, function(result) {
            if (result.status && result.data.employeeList.length !== 0) {
                var candidateList = result.data.employeeList;
                candidateList = $filter('orderBy')(candidateList, 'id', true);
                
                deferred.resolve(candidateList);
            } else {
                deferred.resolve(null);
                $log.info('Da co loi khi search');
            }
        });

        return deferred.promise;
    };

    // Get 10 new candidate
    var getNewCandidate = function() {
        var info = {
                token: $scope.employer.token,
                option: {
                    countryId: '',
                    provinceId: '',
                    categoryId: '',
                    positionId: ''
                }
            };
        getSearchResult(info).then(function(candidateList) {
            if (candidateList) {
                $scope.totalCandidates = candidateList.length + 11561;
                candidateList = candidateList.slice(0, 10);
                
                candidateList.map(function(candidate) {                
                    candidate.categories = candidate.categoryIds.map(function(categoryId) {
                        var category = {
                            id: categoryId,
                            title: getCategoryName(categoryId, $scope.categoriesList)
                        };
                        return category;
                    });
                });

                getFullname(candidateList);
                getLocationName(candidateList, $scope.countriesList, $scope.provinces.availableProvinces);

                $scope.candidateListPagination = candidateList;
            } 
        });
    };

    var getPositionListPromise = function(lang) {
        var deferred = $q.defer();

        if(!localStorageService.get('listPosition_' + lang)){
            $common.getJobPosition({ 'lang': lang }, function(result){
                if(result.status){
                    deferred.resolve(result.data.positionList);
                } else {
                    // $log.info('Khong lay duoc danh sach vi tri');
                    deferred.resolve(null);
                }
            });
        } else{
            deferred.resolve(localStorageService.get('listPosition_' + lang));
        } 

        return deferred.promise;
    };

    var getPositionList = function(lang) {
        getPositionListPromise(lang)
        .then(function(result) {
            if (result) {
                localStorageService.set('listPosition_' + lang, result);
                $scope.positions = {
                    availabePositions: result,
                };
            }            
        });
    };

    getLocation().then(function() {
        return getCategories('en');
    }).then(function() {
        return getCategories('vi');
    }).then(function() {
        return getPositionList('en');
    }).then(function() {
        return getPositionList('vi');
    }).then(function() {
        var provincesLilstOri = localStorageService.get('provincesList');
        setCurrCountry(config.countryId);
        $scope.provincesList = $employee.getProvincesListByCountry(config.countryId, provincesLilstOri);
        localStorageService.set('provincesList', $scope.provincesList);
        setCurrProvince(config.provinceId);
    }).then(function() {
        getNewCandidate();
    });

    $scope.changePage = function(currPage) {
        $window.scrollTo(0, 70);
        calcPagination(currPage);
    };
    
    $rootScope.$watch('language', function(newValue, oldValue)
    {
        if (localStorageService.get("listPosition_" + newValue))
        {
            if(!$scope.positions) {
                $scope.positions = {};
            }
            $scope.positions.availabePositions = localStorageService.get("listPosition_" + newValue);
        }
        if (localStorageService.get("categoriesList_" + newValue))
        {
            $scope.categoriesList = localStorageService.get("categoriesList_" + newValue);
        }
        if (localStorageService.get("provincesList"))
        {
            if(!$scope.provinces) {
                $scope.provinces = {};
            }
            $scope.provinces.availableProvinces = localStorageService.get("provincesList");
        }
    });

    $scope.openModalCvCandidate = function (employee) {
        var modalInstance = $uibModal.open({
            animation: true,
            ariaLabelledBy: 'modal-title',
            ariaDescribedBy: 'modal-body',
            templateUrl: 'modalCvCandidate.html',
            controller: 'ModalCvCandidateCtrl',
            size: 'lg',
             resolve: {
                data: function () {
                    return {
                            employee: {
                                employeeId: employee.employeeId,
                                employeeEmail: false
                            },
                            employer: $scope.employer
                        };
                }
            }
        });

        modalInstance.result.then(function(candidate) {
            if (candidate) {
                $scope.candidateListPagination.map(function(candidateOri) {
                    if (candidateOri.employeeId === candidate.employeeId) {
                        candidateOri.viewed = candidate.viewed;
                    }
                });
            }
        }, function () {
            $log.info('Modal dismissed at: ' + new Date());
        });
    };

    // Search candidate
    $scope.searchCandidate = function() {
        $scope.searching = true;
        var info = getInfoObj();
        getSearchResult(info).then(function(candidateList) {
            if (candidateList) {
                $scope.resultSearch = true;
                setSearchResult(candidateList);
            } else {
                $scope.resultSearch = false;
            }          
        });
    };

    $scope.changeItemsPerPage = function() {
        $scope.itemsPerPage = $scope.optionNumbers.selectedNumber.value;
        $scope.numPages = Math.ceil($scope.candidatesList/$scope.itemsPerPage);

        if ($scope.currPage > $scope.totalPages) {
            $scope.currPage = $scope.totalPages;
        } 

        var start = ($scope.currPage - 1)*$scope.itemsPerPage;
        var end = $scope.currPage*$scope.itemsPerPage;
        $scope.candidateListPagination = $scope.candidatesList.slice(start, end);
    };
});
'use strict';
angular.module('employerModule').controller('CandidateDetailController', function($scope, $rootScope, $common,
    $q, $employer, $employee, $routeParams, $translate, $uibModal, localStorageService,$log, $cache, _, toastr, $location)
{

        $scope.employer = localStorageService.get('userEmployer');
    if (!$scope.employer)
        {
            $location.path("/");
            return;
        }
    var styding = $translate.instant('employee.styding');
    var stydingEnd = $translate.instant('employee.stydingEnd');
    $scope.countriesList = [];
    $scope.provincesList = [];
    $scope.categoriesList = [];
    $scope.candidate = {
        profile: [],
        expList: [],
        eduList: [],
        cerList: [],
        docList: []
    };

    // Get countries and provinces list
    var getLocation = function()
    {
        var deferred = $q.defer();

            $cache.getJobLocation(
            {
                lang: 'vi'
            }, function(result)
            {
                if (result.status)
                {
                    result.data.countryList.unshift(
                    {
                        id: 0,
                        title: 'Tất cả đất nước'
                    });
                    result.data.provinceList.unshift(
                    {
                        id: 0,
                        title: 'Tất cả tỉnh/thành phố'
                    });
                    $scope.countriesList = result.data.countryList;
                    $scope.provincesList = result.data.provinceList;
                    localStorageService.set('countriesList', result.data.countryList);
                    localStorageService.set('provincesList', result.data.provinceList);
                    deferred.resolve(true);
                }
                else
                {
                    $log.info('error', 'Khong lay duoc danh sach country va province');
                    deferred.resolve(true);
                }
            });
      
        return deferred.promise;
    };
    var getRecentlyCompany = function(candidate, expList)
    {
        var company = _.find(expList, function(exp)
        {
            return exp.current === true;
        });
        candidate.recentlyCompany = company ? company.employer : null;
    };
    var getEduLevelsList = function()
    {
        var deferred = $q.defer();
        $scope.eduLevelsList = [];
        if (localStorageService.get('eduLevelsList'))
        {
            $scope.eduLevelsList = localStorageService.get('eduLevelsList');
            deferred.resolve($scope.eduLevelsList);
        }
        else
        {
            $common.getEducationLevel(
            {
                lang: 'vi'
            }, function(result)
            {
                if (result.status)
                {
                    localStorageService.set('eduLevelsList', result.data.eduLevelsList);
                    $scope.eduLevelsList = result.data.levelList;
                }
                else
                {
                    var noEdu = $translate.instant('toaster.noresultEdu');
                    toastr.error(noEdu);
                }
                deferred.resolve($scope.eduLevelsList);
            });
        }
        return deferred.promise;
    };
    var getEduLevelName = function(eduList)
    {
        eduList.map(function(eduObj)
        {
            var levelObj = _.find($scope.eduLevelsList, function(eduLevel)
            {
                return eduObj.levelId === eduLevel.id;
            });
            eduObj.levelName = levelObj.title;
            if (eduObj.status === 'enrolled')
            {
                eduObj.statusVi = styding;
            }
            else
            {
                eduObj.statusVi = stydingEnd;
            }
            return eduObj;
        });
    };
    var getHighestEdu = function(candidate, eduList)
    {
        var level = _.max(eduList, function(edu)
        {
            return edu.levelId;
        });
        candidate.highestEduLevel = level ? level.levelName : null;
    };
    var getFullname = function(candidate)
    {
        if (candidate.name.indexOf('-'))
        {
            var firstname = candidate.name.split('-')[0];
            var lastname = candidate.name.split('-')[1];
            candidate.fullname = firstname + ' ' + lastname;
        }
        else
        {
            candidate.fullname = candidate.name;
        }
    };
    var getLocationName = function(candidate, countriesList, provincesList)
        {
            // Get country name
            var country = _.find(countriesList, function(country)
            {
                return (candidate.countryId === country.id);
            });
            candidate.countryName = country ? country.title : null;
            // Get province name
            var province = _.find(provincesList, function(province)
            {
                return (candidate.provinceId === province.id);
            });
            candidate.provinceName = province ? province.title : null;
        };
        // Get categories list
    var getCategories = function()
        {
            var deferred = $q.defer();
            if (!localStorageService.get('categoriesList'))
            {
                $common.getJobCategory(
                {
                    'lang': 'vi'
                }, function(result)
                {
                    if (result.status)
                    {
                        $scope.categoriesList = result.data.categoryList;
                        localStorageService.set('categoriesList', result.data.categoryList);
                        deferred.resolve(true);
                    }
                    else
                    {
                        $log.info('Khong lay duoc danh sach category');
                        deferred.resolve(false);
                    }
                });
            }
            else
            {
                $scope.categoriesList = localStorageService.get('categoriesList');
                deferred.resolve(true);
            }
            return deferred.promise;
        };
        // Get list positions
    var getPositionListPromise = function()
    {
        var deferred = $q.defer();
        if (!localStorageService.get('listPosition'))
        {
            $common.getJobPosition(
            {
                'lang': 'vi'
            }, function(result)
            {
                if (result.status)
                {
                    deferred.resolve(result.data.positionList);
                }
                else
                {
                    $log.info('Khong lay duoc danh sach vi tri');
                    deferred.resolve(null);
                }
            });
        }
        else
        {
            deferred.resolve(localStorageService.get('listPosition'));
        }
        return deferred.promise;
    };
    var getPositionList = function()
    {
        getPositionListPromise().then(function(result)
        {
            if (result)
            {
                localStorageService.set('listPosition', result);
                $scope.positionList = result;
                $scope.positions = {
                    availabePositions: result,
                    seletedPosition: result[0]
                };
            }
        });
    };
    var getPositionName = function(expList)
    {
        expList.map(function(exp)
        {
            var position = _.find($scope.positionList, function(position)
            {
                return (exp.positionId === position.id);
            });
            exp.positionName = position ? position.title : '';
        });
    };
    getLocation().then(function()
    {
        getPositionList();
    }).then(function()
    {
        return getEduLevelsList();
    }).then(function()
    {
        return getCategories();
    }).then(function()
    {
        var info = {
            token: $scope.employer.token,
            employeeId: $routeParams.employeeId
        };
        $employer.getEmployeeDetail(info, function(result)
        {
            if (result.status)
            {
                var candidate = result.data.employeeDetail;
                getFullname(candidate.profile);
                getLocationName(candidate.profile, $scope.countriesList, $scope.provincesList);
                getRecentlyCompany(candidate.profile, candidate.expList);
                getEduLevelName(candidate.eduList);
                getHighestEdu(candidate.profile, candidate.eduList);
                candidate.expList.map(function(exp)
                {
                    getLocationName(exp, $scope.countriesList, $scope.provincesList);
                    exp.categories = $employee.getCategories(exp.categoryIdList, $scope.categoriesList);
                });
                $scope.candidate.viewed = candidate.viewed;
                $scope.candidate.profile = candidate.profile;
                $scope.candidate.expList = candidate.expList;
                getPositionName(candidate.expList);
                $scope.candidate.eduList = candidate.eduList;
                $scope.candidate.certList = candidate.certList;
                $scope.candidate.docList = candidate.docList;
            }
        });
    });
    $scope.openVideoProfileModal = function(size)
    {
        var modalInstance = $uibModal.open(
        {
            animation: $scope.animationsEnabled,
            templateUrl: 'videoProfileModal.html',
            controller: 'ModalVideoProfileControllerl',
            size: size,
            resolve:
            {
                data: function()
                {
                    return {
                        videoUrl: $scope.candidate.profile.videoUrl
                    };
                }
            }
        });
        modalInstance.result.then(function(selectedItem)
        {
            $scope.selected = selectedItem;
        }, function()
        {
            $log.info('Modal dismissed at: ' + new Date());
        });
    };
    $scope.openConfirmGetContactModal = function(size)
    {
        var modalInstance = $uibModal.open(
        {
            animation: $scope.animationsEnabled,
            templateUrl: 'confirmGetContactModal.html',
            controller: 'InvitationCandidateController',
            size: size,
            resolve:
            {
                data: function()
                {
                    return {
                        videoUrl: $scope.candidate.profile.videoUrl
                    };
                }
            }
        });
        modalInstance.result.then(function(selectedItem)
        {
            $scope.selected = selectedItem;
        }, function()
        {
            $log.info('Modal dismissed at: ' + new Date());
        });
    };
}).controller('ModalVideoProfileControllerl', function($scope, $uibModalInstance, data)
{
    $scope.videoUrl = data.videoUrl;
    $scope.cancel = function()
    {
        $uibModalInstance.dismiss('cancel');
    };
}).controller('ModalConfirmGetContactControllerl', function($scope, $rootScope, $uibModalInstance, $routeParams, $employer)
{
    $scope.cancel = function()
    {
        $uibModalInstance.dismiss('cancel');
    };
    $scope.getContactCandidate = function()
    {
        var displayContact = function()
        {
            var info = {
                token: $scope.employer.token,
                employeeId: $routeParams.employeeId
            };
            $employer.getEmployeeContact(info, function(result)
            {
                if (result.status)
                {
                    $scope.candidate.viewed = result.data.result;
                }
            });
        };

        displayContact();
        $uibModalInstance.dismiss('cancel');
    };
});
'use strict';
angular.module('employerModule')
.controller('CandidateApplyListController', function($scope, $employer, $profile, $translate, $rootScope, $filter, $window, $log, $uibModal, toastr, localStorageService, $location, $q, _, $common) {
    $scope.userEmployer = localStorageService.get('userEmployer');
    var styding         = $translate.instant('employee.styding');
    var stydingEnd      = $translate.instant('employee.stydingEnd');
    var ten     = $translate.instant('tenItemsPerPage'),
        twenty  = $translate.instant('twentyItemsPerPage'),
        thirty  = $translate.instant('thirtyItemsPerPage'),
        fourty  = $translate.instant('fourtyItemsPerPage'),
        fifty   = $translate.instant('fiftyItemsPerPage');

    $scope.videoCandidate = false;
    if (!$scope.userEmployer)
        {
            $location.path("/");
            return;
        }
    var info = {};

    $scope.pageOptions = {
        availableOptions: [
          {value: 10, name: ten},
          {value: 20, name: twenty},
          {value: 30, name: thirty},
          {value: 40, name: fourty},
          {value: 50, name: fifty}
        ],
        selectedOption: {value: 10, name: ten} 
    };

    $scope.showExcelAll = true;
    $scope.candidateListAll = [];

    var getEduLevelsList = function()
    {
        $scope.eduLevelsList = [];
        if (localStorageService.get('eduLevelsList'))
        {
            $scope.eduLevelsList = localStorageService.get('eduLevelsList');
        }
        else
        {
            $common.getEducationLevel(
            {
                lang: 'vi'
            }, function(result)
            {
                if (result.status)
                {
                    localStorageService.set('eduLevelsList', result.data.levelList);
                    $scope.eduLevelsList = result.data.levelList;
                }
            });
        }
    };

    var getAssignment = function(){
        $employer.getAssignment({  token: $scope.userEmployer.token  },
        function(result) {
            if (result.status)
            {
                $scope.listAssignment = result.data.assignmentList;
            }
        });
    };

    function getCandidateApplyList(info) {
        var deffered    = $q.defer();

        $employer.getCandidateApplyList(info, function(result) {
            if (result.status) {
                deffered.resolve(result.data);
            } else {
                $log.info('Khong lay duoc list candidate');
                deffered.resolve(null);
            }
        });

        return deffered.promise;
    }

    var getHighestEdu = function()
    {
        $scope.candidateList.reduce(function(prev, curr)
        {
            return prev.then(function()
            {
                if(curr.eduList && curr.eduList.length > 0) {
                    var level = _.max(curr.eduList, function(edu)
                    {
                        return edu.levelId;
                    });
                    var levelObj = _.find($scope.eduLevelsList, function(eduLevel)
                    {
                        return level.levelId === eduLevel.id;
                    });
                    level.levelName = levelObj.title;
                    curr.profile.highestEduLevel = level ? level.levelName : null;
                }
            });
        }, Promise.resolve());
        _.defer(function(){$scope.$apply();});
    };

    var getNumberExp = function()
    {
        $scope.candidateList.reduce(function(prev, curr)
        {
            return prev.then(function()
            {
                $profile.calcNumberExp(curr);
            });
        }, Promise.resolve());
        _.defer(function(){$scope.$apply();});
    };

    $scope.changeItemsPerPage = function() {
        $scope.itemsPerPage = $scope.pageOptions.selectedOption.value;
        $scope.changePage($scope.currPage);
    }

    $scope.changePage = function(page) {
        $window.scrollTo(0, 0);
        $scope.currPage = page;

        var info = {
            token: $scope.userEmployer.token,
            offset: ($scope.currPage - 1) * $scope.itemsPerPage,
            length: $scope.itemsPerPage,
            count: true
        };

        getCandidateApplyList(info).then(function(data) {
            if (data) {
                $scope.candidateList = data.candidateList;
                $scope.totalCandidates = data.total;
            }
        }).then(function(){
            getHighestEdu();
            getNumberExp();
        });
    };

    $scope.findjob = function(job){
        if(job){
            $scope.showExcelAll = false;
            job = JSON.parse(job);
            $scope.titleJob = job.name;
            var info = {
                token: $scope.userEmployer.token,
                assignmentId: job.id
            };
            $employer.getCandidateByjob(info, function(result){
                if(result.status){
                    $scope.candidateList = result.data;
                    getHighestEdu();
                    getNumberExp();
                } else{
                    $scope.candidateList = null;
                }
                $scope.showExcel = true;
            });
        } else {
            $scope.showExcel = false;
            $scope.showExcelAll = true;
            var info = {
                token: $scope.userEmployer.token,
                offset: 0,
                length: 10,
                count: true
            };
            getCandidateApplyList(info).then(function(data) {
                if (data) {
                    $scope.candidateList = data.candidateList;
                    $scope.totalCandidates = data.total;
                }
            }).then(function(){
                getHighestEdu();
                getNumberExp();
            });
        }
    };

    $scope.exportToExcel = function() {
        var blob = new Blob([document.getElementById('export-to-excel').innerHTML], {
                type: "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet;charset=utf-8"
            });
        saveAs(blob, "Report.xls");
    };

    var getHighestEduAll = function()
    {
        $scope.candidateListAll.reduce(function(prev, curr)
        {
            return prev.then(function()
            {
                curr.profile.highestEduLevel = "";
                if(curr.eduList.length > 0) {
                    var level = _.max(curr.eduList, function(edu)
                    {
                        return edu.levelId;
                    });
                }
                if(level){
                    var levelObj = _.find($scope.eduLevelsList, function(eduLevel)
                    {
                        return eduLevel.id === level.levelId;
                    });
                }

                curr.profile.highestEduLevel = level ? levelObj.title : null;
            });
        }, Promise.resolve());
        _.defer(function(){$scope.$apply();});
    };

    var getNumberExpAll = function()
    {
        $scope.candidateListAll.reduce(function(prev, curr)
        {
            return prev.then(function()
            {
                $profile.calcNumberExp(curr);
            });
        }, Promise.resolve());
        _.defer(function(){$scope.$apply();});
    };

    $scope.exportToExcelAll = function() {
         var blob = new Blob([document.getElementById('export-to-excel-all').innerHTML], {
                type: "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet;charset=utf-8"
            });
        saveAs(blob, "Report_All.xls");
    };

    $scope.openModalCvCandidate = function (employee) {
        var modalInstance = $uibModal.open({
            animation: true,
            windowClass: "modal fade in",
            ariaLabelledBy: 'modal-title',
            ariaDescribedBy: 'modal-body',
            templateUrl: 'modalCvCandidate.html',
            controller: 'ModalCvCandidateCtrl',
            size: 'lg',
            resolve: {
                data: function () {
                    return {
                            employee: employee,
                            employer: $scope.userEmployer,
                            candidateApply: true
                        };
                }
            }
        });

        modalInstance.result.then(function() {}, function () {
            $log.info('Modal dismissed at: ' + new Date());
        });
    };

    $scope.openModalCoverLetter = function (candidate) {
        var modalInstance = $uibModal.open({
            animation: true,
            windowClass: "modal fade in",
            ariaLabelledBy: 'modal-title',
            ariaDescribedBy: 'modal-body',
            templateUrl: 'modalCoverLetter.html',
            controller: 'ModalCoverLetterCtrl',
            size: 'lg',
            resolve: {
                data: function () {
                    return {
                            candidate: candidate
                        };
                }
            }
        });

        modalInstance.result.then(function() {}, function () {
            $log.info('Modal dismissed at: ' + new Date());
        });
    };

    function init() {
        $scope.currPage = 1;
        $scope.itemsPerPage = $scope.pageOptions.selectedOption.value;
        $scope.maxSize = 4;

        var info = {
            token: $scope.userEmployer.token,
            offset: ($scope.currPage - 1) * $scope.itemsPerPage,
            length: $scope.itemsPerPage,
            count: true
        };

        getEduLevelsList();
        getAssignment();

        getCandidateApplyList(info)
        .then(function(data) {
            if (data) {
                $scope.candidateList = data.candidateList;
                $scope.totalCandidates = data.total;
            }
        }).then(function(){
            getHighestEdu();
            getNumberExp();
        });

        var info = {
            token: $scope.userEmployer.token,
            offset: 0,
            length: 20,
            count: true
        };
        info.length = $scope.totalCandidates;

        getCandidateApplyList(info).then(function(data) {
            if (data) {
                $scope.candidateListAll = _.reject(data.candidateList,function(candidate) {
                    return !candidate.profile;
                });
            }
        }).then(function(){
            getHighestEduAll();
        }).then(function(){
            $scope.candidateListAll = getNumberExpAll($scope.candidateListAll);
        });
    }

    init();
});
'use strict';
angular.module('employerModule').controller('AssignmentListController', function($scope, $employer,toastr,
    $translate, $rootScope,  localStorageService, $location, $q,_)
{
    var alert = $translate.instant('toarster.alert');
    var unablejob = $translate.instant('toaster.unablejob');
    $scope.editAssign = function(assign)
    {
        localStorageService.set('chooseAssignment', assign);
        localStorageService.remove("interviewQuestionList");
        localStorageService.remove('interview');
        $location.path("/employer/assignment");
    };

    $scope.deleteAssign = function(assign)
    {
        if (assign.status !== 'initial' && assign.status) {
            toastr.error( alert, unablejob);
        } else
        {
            $employer.deleteAssignment(
             {
                token: $scope.userEmployer.token,
                assignmentId: assign.id
            }, function(result)
            {
                if(result.status){
                    $scope.listAssignment = _.reject($scope.listAssignment,function(ass) {
                        return ass.id === assign.id;
                    });
                }
            });
        }
    };

    $scope.newAssign = function()
    {
        localStorageService.remove('chooseAssignment');
        localStorageService.remove("interviewQuestionList");
        localStorageService.remove('interview');
        $location.path("/employer/assignment");
    };


    function getAssign()
    {
        var info = {
            token: $scope.userEmployer.token
        };
        $employer.getAssignment(info, function(result)
        {
            if (result.status)
            {
                $scope.listAssignment = result.data.assignmentList;
            }
        });
    }

    function init()
    {
        $scope.userEmployer = localStorageService.get("userEmployer");
        if (!$scope.userEmployer)
        {
            $location.path("/");
            return;
        }
        getAssign();
    }

    init();
});
